// components/layouts/Sidebar.tsx
'use client';
import React, { useState } from 'react';
import { 
  FiHome, 
  FiUser, 
  FiFileText, 
  FiImage, 
  FiLayers, 
  FiMail,
  FiTwitter,
  FiLinkedin,
  FiInstagram,
  FiGithub,
  FiMoon,
  FiSun
} from 'react-icons/fi';
import { useTheme } from '@/contexts/ThemeContext';

export default function Sidebar() {
  const { theme, toggleTheme } = useTheme();
  const [isPortuguese, setIsPortuguese] = useState(true);

  const menuItems = [
    { name: 'Home', href: '#home', icon: <FiHome size={24} /> },
    { name: 'About', href: '#about', icon: <FiUser size={24} /> },
    { name: 'Resume', href: '#resume', icon: <FiFileText size={24} /> },
    { name: 'Portfolio', href: '#portfolio', icon: <FiImage size={24} /> },
    { name: 'Services', href: '#services', icon: <FiLayers size={24} /> },
    { name: 'Contact', href: '#contact', icon: <FiMail size={24} /> },
  ];

  const socialLinks = [
    { name: 'Twitter', href: '#', icon: <FiTwitter size={16} /> },
    { name: 'LinkedIn', href: '#', icon: <FiLinkedin size={16} /> },
    { name: 'Instagram', href: '#', icon: <FiInstagram size={16} /> },
    { name: 'Github', href: '#', icon: <FiGithub size={16} /> },
  ];

  return (
    <div 
      className="fixed left-6 top-20 bottom-20 w-64 rounded-2xl border flex flex-col p-4 shadow-2xl transition-all duration-300 backdrop-blur-sm"
      style={{
        backgroundColor: theme === 'dark' ? '#1B1B1B' : 'rgba(255, 255, 255, 0.8)',
        borderColor: theme === 'dark' ? '#374151' : 'rgba(209, 213, 219, 0.6)'
      }}
    >
      
      {/* Controls - Dark/Light e PT/EN */}
      <div className="flex justify-between items-center mb-6 px-2">
        {/* Dark/Light Toggle */}
        <div className="flex items-center space-x-2">
          <FiMoon size={16} className={theme === 'dark' ? 'text-gray-400' : 'text-gray-600'} />
          <button
            onClick={toggleTheme}
            className={`w-12 h-6 flex items-center rounded-full p-1 transition-all duration-300 ${
              theme === 'dark' ? 'bg-gray-700 justify-end' : 'bg-gray-300 justify-start'
            }`}
          >
            <div className="w-4 h-4 bg-white rounded-full shadow-md"></div>
          </button>
          <FiSun size={16} className={theme === 'dark' ? 'text-gray-400' : 'text-gray-600'} />
        </div>

        {/* Language Toggle */}
        <button
          onClick={() => setIsPortuguese(!isPortuguese)}
          className={`px-3 py-1 rounded-full text-sm font-medium transition-all duration-200 ${
            isPortuguese 
              ? 'bg-gray-800 text-white' 
              : theme === 'dark' ? 'bg-gray-700/80 text-gray-300' : 'bg-gray-200/80 text-gray-700'
          }`}
        >
          {isPortuguese ? 'PT' : 'EN'}
        </button>
      </div>

      {/* Menu Navigation */}
      <nav className="flex-1 flex flex-col justify-center">
        <ul className="space-y-2">
          {menuItems.map((item) => (
            <li key={item.href}>
              <a
                href={item.href}
                className={`flex items-center py-2 px-3 rounded-xl transition-all duration-200 group ${
                  theme === 'dark'
                    ? 'text-gray-300 hover:bg-gray-700/80 hover:text-white'
                    : 'text-gray-700 hover:bg-gray-800/80 hover:text-white'
                }`}
              >
                <span className={`mr-3 group-hover:text-white ${
                  theme === 'dark' ? 'text-gray-400' : 'text-gray-500'
                }`}>
                  {item.icon}
                </span>
                <span className="font-medium text-sm">{item.name}</span>
              </a>
            </li>
          ))}
        </ul>
      </nav>

      {/* Social Links */}
      <div className="mt-auto">
        <div className="flex justify-center space-x-3">
          {socialLinks.map((social) => (
            <a
              key={social.name}
              href={social.href}
              className={`w-8 h-8 flex items-center justify-center rounded-lg hover:text-white transition-all duration-200 ${
                theme === 'dark'
                  ? 'bg-gray-800/80 text-gray-400 hover:bg-gray-600'
                  : 'bg-gray-200/80 text-gray-600 hover:bg-gray-800/80'
              }`}
              title={social.name}
            >
              {social.icon}
            </a>
          ))}
        </div>
      </div>
    </div>
  );
}